*, *:before, *:after {
  box-sizing: border-box;
}

body {
  min-height: 100vh;
  font-family: 'Raleway', sans-serif;
}

.container {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.container:hover .top:before, .container:hover .top:after, .container:hover .bottom:before, .container:hover .bottom:after, .container:active .top:before, .container:active .top:after, .container:active .bottom:before, .container:active .bottom:after {
  margin-left: 200px;
  transform-origin: -200px 50%;
  transition-delay: 0s;
}
.container:hover .center, .container:active .center {
  opacity: 1;
  transition-delay: 0.2s;
}

.top:before, .top:after, .bottom:before, .bottom:after {
  content: '';
  display: block;
  position: absolute;
  width: 200vmax;
  height: 200vmax;
  top: 50%;
  left: 50%;
  margin-top: -100vmax;
  transform-origin: 0 50%;
  transition: all 0.5s cubic-bezier(0.445, 0.05, 0, 1);
  z-index: 10;
  opacity: 0.65;
  transition-delay: 0.2s;
}

.top:before {
  transform: rotate(45deg);
  background: #8B8D8E;
}
.top:after {
  transform: rotate(135deg);
  background: #8B8D8E;
}

.bottom:before {
  transform: rotate(-45deg);
  background: #263F6A;
}
.bottom:after {
  transform: rotate(-135deg);
  background: #263F6A;
}

.center {
  position: absolute;
  width: 400px;
  height: 400px;
  top: 50%;
  left: 50%;
  margin-left: -200px;
  margin-top: -200px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 30px;
  opacity: 0;
  transition: all 0.5s cubic-bezier(0.445, 0.05, 0, 1);
  transition-delay: 0s;
  color: #333;
}
.center input {
  width: 100%;
  padding: 15px;
  margin: 5px;
  border-radius: 1px;
  border: 1px solid #ccc;
  font-family: inherit;
}
@media (min-width: 768px){
.containe{
  max-width: 100%;
}}
@media (min-width: 576px){
.container {
  max-width: 100%;
}}
* {
  outline: none;
}
strong {
  font-weight: 600;
}
.page {
  width: 100%;
  height: 100vh;
  font-family: 'Encode Sans Condensed', sans-serif;
  font-weight: 600;
  letter-spacing: .03em;
  color: #212121;
}
header {
  display: flex;
  position: fixed;
  width: 100%;
  height: 70px;
  background: #212121;
  color: #fff;
  justify-content: center;
  align-items: center;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}
main {
  padding: 70px 20px 0;
  display: flex;
  flex-direction: column;
  height: 100%;
}
main > div {
  margin: auto;
}
main p {
  line-height: 1.5;
  font-weight: 200;
  margin: 20px 0;
}
main small {
  font-weight: 300;
  color: #888;
}
.nav-container {
  position: fixed;
  height: 100vh;
  width: 100%;
  pointer-events: none;
}
.nav-container .bg {
  position: absolute;
  top: 70px;
  left: 0;
  width: 100%;
  height: calc(100% - 70px);
  visibility: hidden;
  opacity: 0;
  transition: .3s;
  background: #000;
}
.nav-container:focus-within .bg {
  visibility: visible;
  opacity: .6;
}
.nav-container * {
  visibility: visible;
}
.button {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  z-index: 1;
  -webkit-appearance: none;
  border: 0;
  background: transparent;
  border-radius: 0;
  height: 70px;
  width: 30px;
  cursor: pointer;
  pointer-events: auto;
  margin-left: 25px;
  touch-action: manipulation;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}
.icon-bar {
  display: block;
  width: 100%;
  height: 3px;
  background: #aaa;
  transition: .3s;
}
.icon-bar + .icon-bar {
  margin-top: 5px;
}
.nav-container:focus-within .button {
  pointer-events: none;
}
.nav-container:focus-within .icon-bar:nth-of-type(1) {
  transform: translate3d(0,8px,0) rotate(45deg);
}
.nav-container:focus-within .icon-bar:nth-of-type(2) {
  opacity: 0;
}
.nav-container:focus-within .icon-bar:nth-of-type(3) {
  transform: translate3d(0,-8px,0) rotate(-45deg);
}
.nav-content {
  margin-top: 70px;
  padding: 20px;
  width: 20%;
  max-width: 300px;
  position: absolute;
  top: 0;
  left: 0;
  height: calc(100% - 70px);
  background: #2121217a;
  pointer-events: auto;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  transform: translateX(-100%);
  transition: transform .3s;
  will-change: transform;
  contain: paint;
}
.nav-content ul {
  height: 100%;
  display: flex;
  flex-direction: column;
}
.nav-content li  {
  padding: 10px 5px;
  display: block;
  text-transform: uppercase;
  transition: color 1.1s;
}
.nav-content li:hover {
  cursor: pointer;
  color: lightsteelblue;
}
.nav-content li:not(.small) + .small {
  margin-top: auto;
}
.small {
  display: flex;
  align-self: center;
}
.small li {
  font-size: 12px;
  font-weight: 400;
  color: #aaa;
}
.small li + li {
  margin-left: 15px;
}
.nav-container:focus-within .nav-content {
  transform: none;
}
